// Example snippet for ConversationsList: use conversation.pseudo when building links
import Link from "next/link";
import React from "react";

export default function ConversationsList({ conversations = [] }) {
  return (
    <div className="conversations-list">
      {conversations.map((conversation) => (
        <Link
          key={conversation._id}
          // previously: `/uis/chat/${conversation.id}` or `${conversation._id}`
          href={`/uis/chat/${conversation.pseudo}`}
        >
          <a className="conversation-item">
            <div className="avatar">
              <img src={conversation.avatar} alt={conversation.pseudo} />
            </div>
            <div className="meta">
              <div className="name">{conversation.pseudo}</div>
              <div className="last-message">{conversation.lastMessage}</div>
            </div>
          </a>
        </Link>
      ))}
    </div>
  );
}